@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

body,
h1,
h2,
h3,
h4,
p,
li {
  margin: 0;
}

html {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

a {
  text-decoration: none;
  font-size: 2em;
}
a:visited {
  color: inherit;
}

i {
  color: #ffffff;
  transition: all 0.3s;
  font-size: 1.5em;
  pointer-events: none;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btn {
  width: 3em;
  border: none;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.btn:hover > i {
  transform: scale(1.4);
}

.main {
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main__form {
  display: flex;
  margin-bottom: 2em;
}
.main__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  width: 50%;
}
@media (max-width: 992px) {
  .form {
    width: 80%;
  }
}
@media (max-width: 576px) {
  .form {
    width: 100%;
  }
}
.form__input {
  border-radius: 0;
  border-color: #000000;
  font-size: 1em;
  font-weight: 700;
  padding: 0.5em;
  width: 100%;
}
.form__input:focus {
  outline: none;
}
.form__submit {
  background-color: #000000;
}

.list {
  width: 100%;
  list-style: none;
}
.list__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5em;
}
.list__item:first-child {
  position: relative;
}
.list__item:first-child::before {
  content: "";
  background-color: black;
  box-shadow: 0px 2px 5px 0px #000000;
  position: absolute;
  top: -1.5em;
  height: 2px;
  width: 100%;
}

.item {
  width: 80%;
  border: 1px solid #000000;
}
@media (max-width: 576px) {
  .item {
    width: 100%;
  }
}
.item__check {
  background-color: #44ad34;
}
.item__check:hover {
  background-color: #328226;
}
.item__text {
  width: 100%;
  padding: 0.5em;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  background-color: #ffffff;
  word-break: break-word;
}
.item__text--checked {
  text-decoration: line-through;
  color: #656565;
  background-color: rgba(68, 173, 52, 0.1333333333);
}
.item__delete {
  background-color: #ff0000;
}
.item__delete:hover {
  background-color: #c00000;
}